import React from "react";
import "../../pages/Service/ServicesPage.css";
import { Link } from "react-router-dom";
import { ButtomGet } from "../ButtomGet/ButtomGet";

/* Multi idioma */
import { FormattedMessage } from "react-intl";

const Service = () => (
  <section className="servicios" id="servicios">
    <h2 className="heading">
      <FormattedMessage id="servdices" defaultMessage="私がやること？" />
    </h2>
    <div className="row service_section">
      <div className="columns" data-aos="fade-up" data-aos-delay="300">
        <i className="fas fa-laptop"></i>
        <h3>
          <FormattedMessage id="develdopment" defaultMessage="Web開発" />
        </h3>
        <p>
          <FormattedMessage
            id="develodpment-info"
            defaultMessage="適切に構造化された Web ページ、優れたレスポンシブ デザイン、魅力的なカラー パレット、Web サイトの閲覧時にユーザーに満足感を与えるインタラクションを備えた Web ページの作成。"
          />
        </p>
      </div>
      <div className="columns" data-aos="fade-up" data-aos-delay="300">
        <i className="fas fa-mobile-alt"></i>
        <h3>
          <FormattedMessage
            id="develdopments"
            defaultMessage="モバイルアプリ開発"
          />
        </h3>
        <p>
          <FormattedMessage
            id="develodddpment-infos"
            defaultMessage="モバイル/デスクトップ アプリ開発の専門知識を活かして、Android/iOS および Windows/Linux/MacOS に特化した高性能モバイル/デスクトップ アプリケーションを構築できます。 アプリはオペレーティング システムの可能性を最大限に活用し、最適なパフォーマンスと信頼性を実現します。"
          />
        </p>
      </div>
      <div className="columns" data-aos="fade-up" data-aos-delay="200">
        <i className="fas fa-drafting-compass"></i>
        <h3>
          <FormattedMessage
            id="desddign"
            defaultMessage="デザイン"
          />
        </h3>
        <p>
          <FormattedMessage
            id="desidgdn-info"
            defaultMessage="ユーザーを魅了し、ブランドの本質を反映する素晴らしいデザインを作成します。 アイデアを視覚的に魅力的でユーザーフレンドリーなデザインに変換します。 オンラインでのプレゼンスを実現しましょう!"
          />
        </p>
      </div>
    </div>
    <div className="portafolio-btn">
      <Link to="/service">
        <ButtomGet />
      </Link>
    </div>
  </section>
);

export default React.memo(Service);
